h1 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--yellow);
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  max-width: 500px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #333;
  border-radius: 8px;
  border: 1px solid var(--yellow);
}
form div {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

label {
  font-weight: bold;
  color: var(--txt);
}

input[type=text] {
  padding: 0.8rem;
  border: 1px solid #555;
  background-color: #444;
  color: var(--txt);
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.3s, box-shadow 0.3s;
}
input[type=text]:focus {
  outline: none;
  border-color: var(--yellow);
  box-shadow: 0 0 5px rgba(255, 215, 0, 0.5);
}
input[type=file] {
  color: #ccc;
}
input[type=file]::file-selector-button {
  padding: 0.8rem 1.2rem;
  background-color: var(--yellow);
  color: var(--back);
  border: none;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-right: 1rem;
}
input[type=file]::file-selector-button:hover {
  background-color: #e6c300;
}
input[type=submit] {
  padding: 0.8rem;
  background-color: var(--green);
  color: var(--back);
  border: none;
  border-radius: 4px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}
input[type=submit]:hover {
  background-color: #218c4a;
}

textarea {
  padding: 0.8rem;
  border: 1px solid #555;
  background-color: #444;
  color: var(--txt);
  border-radius: 4px;
  transition: border-color 0.3s, box-shadow 0.3s;
  max-width: 100%;
  min-width: 100%;
  min-height: 2rem;
}
textarea:focus {
  outline: none;
  border-color: var(--yellow);
  box-shadow: 0 0 5px rgba(255, 215, 0, 0.5);
}

.message {
  text-align: center;
  margin-top: 1rem;
  min-height: 1.2em;
  font-weight: bold;
}